import {
  BaseQueryFn,
  createApi,
  FetchArgs,
  fetchBaseQuery,
  FetchBaseQueryError,
  FetchBaseQueryMeta,
  retry
} from "@reduxjs/toolkit/query/react";
import {QueryReturnValue} from "@reduxjs/toolkit/dist/query/baseQueryTypes";

// Create our baseQuery instance
const baseQuery = fetchBaseQuery({
  baseUrl: API_URL,
  // prepareHeaders: (headers, { getState }) => {
  //   // By default, if we have a token in the store, let's use that for authenticated requests
  //   const token = (getState() as RootState).auth.token;
  //   if (token) {
  //     headers.set("authentication", `Bearer ${token}`);
  //   }
  //   return headers;
  // },
});

const baseQueryWithIntercept: BaseQueryFn<
  string | FetchArgs,
  unknown,
  FetchBaseQueryError
  > = async (args, api, extraOptions) => {
  const result: QueryReturnValue<
    any,
    FetchBaseQueryError,
    FetchBaseQueryMeta
    > = await baseQuery(args, api, extraOptions);
  console.log(result, '');
  const { data, error, meta } = result;
  // 
  if (error) {
    const { status } = error as FetchBaseQueryError;
    const { request } = meta as FetchBaseQueryMeta;
    const url: string = request.url;
    // 
    printHttpError(Number(status), url);
    // TODO 
  }
  if (Object.is(data?.code, 0)) {
    return result;
  }
  throw new Error(data.message);
};

export const printHttpError = (httpStatus: number, path: string): void => {
  switch (httpStatus) {
    case 400:
      console.log(`:${path}`);
      break;
    // 401: 
    // 
    case 401:
      console.log(',');
      window.location.reload();
      break;
    // 
    case 403:
      console.log('');
      // 
      window.localStorage.clear();
      window.location.reload();
      break;
    // 404
    case 404:
      console.log('');
      break;
    // 
    default:
      console.log('');
      break;
  }
};

const baseQueryWithRetry = retry(baseQuery, { maxRetries: 2 });

export const base = createApi({
  reducerPath: "daily",
  baseQuery: baseQueryWithRetry,
  endpoints: (build) => ({}),
});
